* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Visuelt Pro", sans-serif;
}

html,
body {
  background-color: #fafafa; /* fafafa */
  scroll-behavior: smooth;
}

nav {
  height: 100vh;
}

main {
  padding: 25vh;
  padding-top: 0;
  padding-bottom: 0;
  z-index: 10;
}

/* HOME PAGE FIXED */

.title-name {
  font-size: 5rem;
  font-weight: 400;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-family: "Edensor", sans-serif;
  color: black;
  width: max-content;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
}

.date {
  position: fixed;
  bottom: 10%;
  right: 5%;
}

.home-about {
  display: flex;
  align-items: center;
  position: fixed;
  top: 10%;
  right: 5%;
  gap: 10px;
}

.propos {
  text-decoration: none;
  color: black;
}

.propos:hover {
  text-decoration: underline;
}

.dev {
  position: fixed;
  bottom: 10%;
  left: 5%;
}

.ng {
  position: fixed;
  left: 5%;
  top: 10%;
  font-size: 1rem;
  color: black;
  font-family: "Aubrey", sans-serif;
}

.warning {
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  top: 10%;
  font-size: 0.8rem;
  color: rgb(255, 81, 0);
}

/* PROJECTS */

.projects {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 15vh;
  align-items: center;
}

/* .project:nth-child(odd) {
  margin-left: auto;
} */

.container-colors {
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 20px;
  margin-top: 5vh;
  margin-bottom: 3vh;
}

.color {
  width: 100px;
  height: 100px;
}

.font-used {
  font-size: 1.5rem
}

.visuel {
 font-weight: 500;
}

@media screen and (max-width: 768px) {
  .title-name {
    font-size: 2rem;
  }

  .project img {
    width: 50vw;
  }
  .project {
    width: fit-content;
  }

  .project p {
    width: 200px;
  }

  .projects {
    align-items: center;
  }
  .project:nth-child(odd) {
    margin-left: 0;
  }

  main {
    padding: 0;
  }
  .first-section:nth-child(1) {
    margin-top: 15vh;
  }

  .email {
    font-size: 1rem;
  }
}
